// Admin page background and footer styles
body.toplevel_page_block-a11y-checks,
body[class*="block-checks_page_block-a11y-checks-"] {
    background-color: var(--a11y-black);

    #wpfooter {
        color: #a3a3a3;

        a {
            color: #fff;
        }
    }
}

// Primary settings wrapper
.ba11y-settings {
    background-color: var(--a11y-black);
    margin: 0 0 0 -20px;
    padding: 15px 15px 0 0;

    // Reset margins and padding for all headings and paragraphs
    h1, h2, h3, p {
        margin: 0;
        padding: 0;
    }
}

// Secondary settings container
.ba11y-settings-container {
    // background-color: var(--a11y-lightest-grey);
    background-color: #fff;
    border-radius: 8px;
    overflow: hidden;
}

// Header section
.ba11y-settings-header {
    padding: 30px;

    h1 {
        font-size: 1.3rem;
        margin: 0 0 10px 0;
    }
    
    p {
        font-size: 0.85rem;
        font-style: italic;
        color: var(--a11y-medium-grey);
        margin: 0;
    }
}

// Plugin options header
.ba11y-settings-plugin-header, .ba11y-settings-meta-header {
    position: relative;
    background-color: #fff;
    padding: 24px 30px;
    margin: 50px 0 30px 0;
    border-top: 2px solid var(--a11y-blue);

    &:first-of-type {
        margin-top: 0;
    }

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: var(--a11y-blue);
        opacity: 0.1;
        z-index: 1;
    }

    h2 {
        color: var(--a11y-black);
        position: relative;
        z-index: 1;
    }

    .plugin-version {
        font-style: italic;
        margin-top: 4px;
    }
}

// Form styles and field layouts
.ba11y-settings-form {
    background-color: #fff;
}

// Individual block group settings
.ba11y-block-options {
    max-width: 896px;
    padding: 0 30px;
}

// Add margin between block options
.ba11y-block-options + .ba11y-block-options,
.ba11y-settings-form .button {
    margin-top: 32px;
}

// Block options heading styles
.ba11y-block-options h2 {
    margin-bottom: 12px;
}

// Accessible group layout using role="group" instead of fieldset
.ba11y-block-single-option {
    border-bottom: 1px solid var(--a11y-lightest-grey);
    padding: 12px 0;

    &:hover {
        background-color: #fbfbfb;
    }   
}

.ba11y-field-group {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
    gap: 16px;
    
    @media (min-width: 783px) {
        flex-direction: row;
        align-items: center;
    }
}

// Label / description text styles
.ba11y-field-label p {
        color: var(--a11y-black);
        font-size: 0.8rem;
        line-height: 1.4;
}
 // Control buttons wrapper styles
.ba11y-field-controls {
    display: flex;
    gap: 0;
    flex-wrap: wrap;
}

.ba11y-field-controls--checkbox {
    gap: 20px;
    padding: 6px 0;

    .ba11y-checkbox-item {
        display: flex;
        gap: 5px;
        align-items: center;
    }

    input[type="checkbox"] {
        width: 20px;
        height: 20px;
        margin: 0;
        border-radius: 0;
        border: 1px solid var(--a11y-medium-grey);
        box-shadow: none;

        &:checked {
            background-color: var(--a11y-blue);
            border: 1px solid var(--a11y-blue);
        }

        &:checked::before {
            content: '';
        }
    }

    label {
        margin: 0;
        font-size: 0.75rem;
        font-weight: 400;
        color: #374151;
        cursor: pointer;
    }
}

.ba11y-field-controls--radio {
    border: 1px solid var(--a11y-blue);
    border-radius: 4px;
    overflow: hidden;
    
    input[type="radio"] {
        display: none;
    }
    
    label.ba11y-button {
        padding: 6px 12px;
        background-color: #fff;
        cursor: pointer;
        transition: all 0.2s ease;
        font-size: 0.75rem;
        font-weight: 400;
        color: var(--a11y-blue);
        user-select: none;
        
        &:first-of-type {
            border-right: 1px solid var(--a11y-blue);
        }
        
        &:last-of-type {
            border-left: 1px solid var(--a11y-blue);
        }
        
        &:hover {
            background-color: var(--a11y-light-blue);
            color: var(--a11y-black);
        }
    }
    
    input[type="radio"]:checked + label.ba11y-button {
        background-color: var(--a11y-blue);
        color: #fff;
    }
    
    // Focus styles for accessibility
    input[type="radio"]:focus + label.ba11y-button {
        outline: 2px solid var(--a11y-blue);
        outline-offset: 2px;
    }
}

.ba11y-settings-submit {
    p {
        padding: 0 30px 30px 30px;
    }

    .button {
        font-size: 0.9rem;
        margin-top: 48px;
        padding: 8px 16px;
        background-color: var(--a11y-blue);
        border-color: var(--a11y-blue);
        
        &:hover {
            background-color: var(--a11y-black);
            border-color: var(--a11y-black);
        }
    }
}

// Success notices
.ba11y-settings-notice {
    background-color: #fff;
    color: #000;
    margin: 30px 30px 30px 27px;
    padding: 10px;
    border-radius: 4px;
}